	padding: 5px 0 0 0;
}

.radio-custom .checkmark:after {
	content: "";
	position: absolute;
	display: none;
}

.radio-custom input:checked~.checkmark~span,
.radio-custom:hover input~.checkmark~span {
	color: #EE0033;
}

.radio-custom input:checked~.checkmark:after {
	display: block;
	border: 0;
	font-family: 'icon-theme';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
