.radio-custom1 {
	display: block;
	position: relative;
	padding-left: 24px;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	line-height: 20px;
}

/* Hide the browser's default radio button */
.radio-custom1 input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}

/* Create a custom radio button */
.radio-custom1 .checkmark {
	position: absolute;
	top: 1px;
	left: 0;
	height: 20px;
	width: 20px;
	border: 2px solid #DADADA;
	border-radius: 100%;
}

.radio-custom1:hover input~.checkmark {
	border-color: #EE0033;
}

.radio-custom1 input:checked~.checkmark {
	border-color: #14afb4;
}

.radio-custom1 span {
	display: block;
}

.radio-custom1 .price {
	padding: 5px 0 0 0;
}

.radio-custom1 .checkmark:after {
	content: "";
	position: absolute;
	display: none;
}

.radio-custom1 input:checked~.checkmark~span {
	font-weight: 500;
}

.radio-custom1 input:checked~.checkmark:after {
	display: block;
	background: #EE0033;
	width: 10px;
	height: 10px;
	border-radius: 100%;
	position: absolute;
	top: 3px;
	left: 3px;
}

