select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  border: 1px solid #E7E7E7;
  box-sizing: border-box;
  border-radius: 10px;
  width: 100%;
  line-height: 20px;
  padding: 15px 16px;
  background: #fff url('../images/select.svg') no-repeat;
  background-position-x: calc(100% - 16px);
  background-position-y: 46%;
  font-weight: 500;
  color: #576C8A;
  font-family: "Roboto", sans-serif;
}
input,
textarea {
  border: 1px solid #E7E7E7;
  box-sizing: border-box;
  border-radius: 10px;
  width: 100%;
  font-weight: 500;
  color: #576C8A;
  line-height: 20px;
  padding: 15px 16px;
  font-family: "Roboto", sans-serif;
}
select,
input {
  outline: 0;
}
select.error,
input.error,
textarea.error,
select.error:focus,
input.error:focus,
textarea.error:focus {
  border-color: #FF0000;
  color: #576C8A;
}
div.error {
  color: #FF0000;
  font-style: italic;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  padding: 0 0 3px;
}
