.radio-custom {
  display: block;
  position: relative;
  padding-left: 24px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
/* Hide the browser's default radio button */
.radio-custom input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
/* Create a custom radio button */
.radio-custom .checkmark {
  position: absolute;
  top: 1px;
  left: 0;
  height: 15px;
  width: 15px;
  border: 1px solid #576C8A;
  border-radius: 100%;
}
.radio-custom:hover input ~ .checkmark {
  border: 1px solid #EE0033;
}
.radio-custom input:checked ~ .checkmark {
  border: 0;
}
.radio-custom span {
  display: block;
}
.radio-custom .price {
  padding: 5px 0 0 0;
}
.radio-custom .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.radio-custom input:checked ~ .checkmark ~ span,
.radio-custom:hover input ~ .checkmark ~ span {
  color: #EE0033;
}
.radio-custom input:checked ~ .checkmark:after {
  display: block;
  border: 0;
  font-family: 'icon-theme';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: '\e912';
  color: #EE0033;
  font-size: 17px;
  margin: -1px 0 0 0;
}
